<template>
  <div>
    <b-list-group>
      <b-list-group-item
        v-for="(sala, index) in salas"
        :key="sala.id"
        href="#"
        @click="seleccionarSala(sala.id, index)"
        :class="[selSala === sala.id ? 'shadow' : '', 'sala', selSala === sala.id ? 'border-secondary' : '', selSala === sala.id ? 'bg-light' : '', 'd-flex item', 'mb-1']"
      >
        <div class="ml-4">
          <h5>Sala #{{ sala.numero }}</h5>
        </div>
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "ListaSalas",
  props: {
    salas: {
      type: Array,
      default: null
    }
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters("salasFunciones", ["selSala"])
  },
  methods: {
    seleccionarSala(id, index) {
      this.updateSala(this.salas[index]);
      this.updateSelSala(id);
    },
    ...mapMutations("salasFunciones", ["updateSala", "updateSelSala"])
  },
  destroyed() {
    this.updateSelSala(null);
  }
};
</script>

