<template>
  <div>
    <NavSalasFunciones/>
    <Salas v-if="salas" />
    <Funciones v-if="funciones" />
  </div>
</template>

<script>
import Salas from "@/components/Salas.vue";
import NavSalasFunciones from "@/components/SalasYFunciones/NavSalasFunciones.vue";
import Funciones from "@/components/Funciones.vue";
import { mapMutations, mapActions, mapGetters } from "vuex";

export default {
  components: {
    Salas,
    Funciones,
    NavSalasFunciones
  },
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters(["salas", "funciones"])
  },
  methods: {
    ...mapMutations(["addBreadcrumb", "setTabSalasFunciones"]),
    ...mapActions("salasFunciones", ["loadSalas", "loadFunciones"])
  },
  created() {
    this.loadSalas();
    this.loadFunciones();
    this.addBreadcrumb([
      {
        text: "Home",
        to: { name: "Home" }
      },
      {
        text: "Salas y Funciones",
        to: { name: "Salas y Funciones" }
      }
    ]);
  }
};
</script>

