<template>
  <div>
    <b-modal
      id="gestionSala"
      :title="modo + ' Sala'"
    >
      <form @submit.prevent="">
        <b-form-input
          type="number"
          placeholder="Introduzca numero de sala"
          v-model="numero"
        ></b-form-input>
      </form>
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button
          v-if="modo === 'Agregar'"
          size="sm"
          variant="success"
          @click="
            ok();
            insertarSala();
          "
        >
          Agregar
        </b-button>
        <b-button
          v-else
          size="sm"
          variant="success"
          @click="
            ok();
            modificarSala();
          "
        >
          Modificar
        </b-button>
        <b-button
          size="sm"
          variant="danger"
          @click="cancel()"
        >
          Cancelar
        </b-button>
      </template>
    </b-modal>
    <b-navbar-nav>
      <b-button-group>
        <b-button
          variant="primary"
          v-b-modal.gestionSala
          @click="setModo('Agregar')"
        >
          <i
            class="fa fa-plus-circle"
            aria-hidden="true"
          ></i> Agregar Sala
        </b-button>
        <b-button
          variant="warning"
          v-b-modal.gestionSala
          @click="setModo('Editar')"
          :disabled="sala == null"
        >
          <i class="fas fa-edit "></i> Editar Sala
        </b-button>
        <b-button
          variant="danger"
          @click="confirmacionEliminar()"
          :disabled="sala == null"
        >
          <i
            class="fa fa-trash"
            aria-hidden="true"
          ></i> Eliminar Sala
        </b-button>
      </b-button-group>
    </b-navbar-nav>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import notificacion from "@/mixin/notificacion";
export default {
  name: "NavSalas",
  mixins: [notificacion],
  data() {
    return {
      numero: "",
      modo: "Agregar",
      idSala: ""
    };
  },
  computed: {
    ...mapGetters("salasFunciones", ["sala"])
  },
  methods: {
    ...mapActions("salasFunciones", [
      "insertSala",
      "loadSalas",
      "deleteSala",
      "modifySala"
    ]),
    async insertarSala() {
      const result = await this.insertSala({ numero: this.numero });
      if (!result.error) {
        this.loadSalas();
        this.numero = "";
      }
      this.show(result, "Sala", "agregada");
    },
    async modificarSala() {
      const result = await this.modifySala({
        numero: this.numero,
        id: this.sala.id
      });
      if (!result.error) {
        this.loadSalas();
        this.numero = "";
      }
      this.show(result, "Sala", "modificada");
    },
    async eliminarSala() {
      const result = await this.deleteSala(this.sala.id);
      if (!result.error) this.loadSalas();
      this.show(result, "Sala", "eliminada");
    },
    async confirmacionEliminar() {
      const resultado = await this.$bvModal.msgBoxConfirm(
        "Â¿Desea eliminar la sala?",
        {
          title: "Confirme",
          size: "sm",
          buttonSize: "sm",
          okVariant: "danger",
          okTitle: "Si",
          cancelTitle: "No",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true
        }
      );
      if (resultado) this.eliminarSala();
    },
    setModo(modo) {
      this.modo = modo;
      if (modo === "Agregar") {
        this.numero = "";
      } else {
        this.numero = this.sala.numero;
      }
    }
  }
};
</script>

