<template>
  <div>
    <b-modal
      id="gestionFuncion"
      :title="modo + ' Funcion'"
    >
      <b-card
        body-class="text-center"
        header-tag="nav"
      >
        <template v-slot:header>
          <b-nav card-header>
            <b-nav-text>Seleccione un Contenido: </b-nav-text>
            <b-nav-item-dropdown
              id="my-nav-dropdown"
              :text="tipoContenido"
              toggle-class="nav-link-custom"
              right
            >
              <b-dropdown-item @click="setContenido(0)">Estrenos</b-dropdown-item>
              <b-dropdown-item @click="setContenido(1)">Series</b-dropdown-item>
              <b-dropdown-item @click="setContenido(2)">Contemporaneos</b-dropdown-item>
              <b-dropdown-item>Cartelera</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-nav>
        </template>
        <ListaContenido :contenidos="catalogo" />
      </b-card>
      <b-card class="mt-2">
        <template v-slot:header>
          <b-nav card-header>
            <b-nav-text>Seleccione una Sala </b-nav-text>
          </b-nav>
        </template>
        <ListaSalas :salas="salas" />
      </b-card>
      <b-card class="mt-2">
        <template v-slot:header>
          <b-nav card-header>
            <b-nav-text>Complete los datos </b-nav-text>
          </b-nav>
        </template>
        <form @submit.prevent="">
          <b-form-group
            id="input-group-fecha"
            label="Fecha de estreno:"
            label-for="fecha"
          >
            <b-input-group class="mb-3">
              <b-form-input
                id="example-input"
                v-model="fecha"
                type="text"
                placeholder="YYYY-MM-DD"
              ></b-form-input>
              <b-input-group-append>
                <b-form-datepicker
                  v-model="fecha"
                  button-only
                  right
                  locale="es-ES"
                  required
                ></b-form-datepicker>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
          <b-form-group
            id="input-group-hora-inicio"
            label="Hora Inicio:"
            label-for="hora-inicio"
          >
            <b-form-select
              id="hora-inicio"
              v-model="horaInicio"
              :options="horasInicio"
              required
            ></b-form-select>
          </b-form-group>
          <b-form-group
            id="input-group-hora-fin"
            label="Hora Fin:"
            label-for="hora-fin"
          >
            <b-form-select
              id="hora-fin"
              v-model="horaFin"
              :options="horasFin"
              required
            ></b-form-select>
          </b-form-group>
        </form>
      </b-card>
      <template v-slot:modal-footer="{ ok, cancel }">
        <b-button
          v-if="modo === 'Agregar'"
          size="sm"
          variant="success"
          :disabled="datosCompletos"
          @click="
            ok();
            insertarFuncion();
          "
        >
          Agregar
        </b-button>
        <b-button
          v-else
          size="sm"
          variant="success"
          @click="
            ok();
            modificarFuncion();
          "
        >
          Modificar
        </b-button>
        <b-button
          size="sm"
          variant="danger"
          @click="cancel()"
        >
          Cancelar
        </b-button>
      </template>
    </b-modal>
    <b-navbar-nav>
      <b-button-group>
        <b-button
          variant="primary"
          v-b-modal.gestionFuncion
          @click="setModo('Agregar')"
        >
          <i
            class="fa fa-plus-circle"
            aria-hidden="true"
          ></i> Agregar Funcion
        </b-button>
        <b-button
          variant="warning"
          v-b-modal.gestionFuncion
          @click="setModo('Editar')"
          :disabled="funcion == null"
        >
          <i class="fas fa-edit "></i> Editar Funcion
        </b-button>
        <b-button
          variant="danger"
          @click="confirmacionEliminar()"
          :disabled="funcion == null"
        >
          <i
            class="fa fa-trash"
            aria-hidden="true"
          ></i> Eliminar Funcion
        </b-button>
      </b-button-group>
    </b-navbar-nav>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import ListaContenido from "@/components/ListaContenido.vue";
import ListaSalas from "@/components/ListaSalas.vue";
import notificacion from "@/mixin/notificacion";
export default {
  name: "NavFunciones",
  mixins: [notificacion],
  components: {
    ListaContenido,
    ListaSalas
  },
  data() {
    return {
      modo: "Agregar",
      fecha: "",
      horaInicio: null,
      horaFin: null,
      id: "",
      horasInicio: [
        { value: null, text: "Por favor seleccione una hora de inicio" },
        { value: "08:00:00", text: "08:00 AM" },
        { value: "11:00:00", text: "11:00 AM" },
        { value: "13:00:00", text: "01:00 PM" },
        { value: "16:00:00", text: "04:00 PM" }
      ],
      horasFin: [
        { value: null, text: "Por favor seleccione una hora de finalizacion" },
        { value: "11:00:00", text: "11:00 AM" },
        { value: "13:00:00", text: "01:00 PM" },
        { value: "16:00:00", text: "04:00 PM" },
        { value: "20:00:00", text: "08:00 PM" }
      ],
      idContenido: "",
      idSala: "",
      tipoContenido: "Estreno"
    };
  },
  computed: {
    ...mapGetters("salasFunciones", ["funcion", "salas", "sala"]),
    ...mapGetters("Catalogo", ["peliculas", "catalogo", "tipo", "contenido"]),
    datosCompletos() {
      return (
        this.idSala === "" &&
        this.idContenido === "" &&
        this.fecha === "" &&
        this.horaInicio === null &&
        this.horaFin === null
      );
    },
    getFuncion() {
      return {
        id_contenido:
          this.funcion == null ? this.contenido.id : this.idContenido,
        id_sala: this.funcion == null ? this.sala.id : this.idSala,
        fecha: this.fecha,
        hora_inicio: this.horaInicio,
        hora_fin: this.horaFin,
        id: this.id
      };
    }
  },
  methods: {
    ...mapActions("Catalogo", ["cargarPeliculas", "cargarSeries"]),
    ...mapActions("salasFunciones", [
      "insertFuncion",
      "loadFunciones",
      "deleteFuncion",
      "modifyFuncion"
    ]),
    ...mapMutations("salasFunciones", ["updateSelSala"]),
    ...mapMutations("Catalogo", ["updateSelContenido"]),
    async insertarFuncion() {
      const result = await this.insertFuncion(this.getFuncion);
      if (!result.error) {
        this.loadFunciones();
        this.sala = null;
        this.contenido = null;
        this.fecha = "";
        this.horaInicio = null;
        this.horaFin = null;
      }
      this.show(result, "Funcion", "agregada");
    },
    async modificarFuncion() {
      const result = await this.modifyFuncion(this.getFuncion);
      if (!result.error) {
        this.loadFunciones();
        this.idSala = null;
        this.idContenido = null;
        this.fecha = "";
        this.horaInicio = null;
        this.horaFin = null;
      }
      this.show(result, "Funcion", "modificada");
    },
    async borrarFuncion() {
      const result = await this.deleteFuncion(this.funcion.id);
      if (!result.error) this.loadFunciones();
      this.show(result, "Funcion", "eliminada");
    },
    async confirmacionEliminar() {
      const resultado = await this.$bvModal.msgBoxConfirm(
        "Â¿Desea eliminar la funcion?",
        {
          title: "Confirme",
          size: "sm",
          buttonSize: "sm",
          okVariant: "danger",
          okTitle: "Si",
          cancelTitle: "No",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true
        }
      );
      if (resultado) this.borrarFuncion();
    },
    setModo(modo) {
      this.modo = modo;
      this.idContenido = modo === "Agregar" ? "" : this.funcion.id_contenido;
      this.idSala = modo === "Agregar" ? "" : this.funcion.id_sala;
      this.fecha = modo === "Agregar" ? "" : this.funcion.fecha;
      this.horaInicio = modo === "Agregar" ? null : this.funcion.hora_inicio;
      this.horaFin = modo === "Agregar" ? null : this.funcion.hora_fin;
      this.id = modo === "Agregar" ? "" : this.funcion.id;
      if (modo === "Editar") {
        this.updateSelSala(this.funcion.id_sala);
        this.updateSelContenido(this.funcion.id_contenido);
      }
    },
    setContenido(tcontenido) {
      switch (tcontenido) {
        case 0:
          this.cargarPeliculas(0);
          this.tipoContenido = "Estrenos";
          break;
        case 1:
          this.cargarSeries();
          this.tipoContenido = "Series";
          break;
        case 2:
          this.cargarPeliculas(1);
          this.tipoContenido = "Contemporaneas";
          break;
        default:
          break;
      }
    }
  },
  created() {
    this.cargarSeries();
    this.cargarPeliculas(0);
  }
};
</script>

<style lang="scss" scoped>
</style>