<template>
  <div>
    <b-card no-body>
      <b-card-title class="mt-2">Sala #{{ sala == null ? '' : sala.numero }}</b-card-title>
      <b-card-text>
        <b-list-group>
          <b-list-group-item
            class="mt-1"
            variant="success"
          >
            Funcion Anterior: {{ funcionAnterior.contenido.Titulo }}
          </b-list-group-item>
          <b-list-group-item
            class="mt-1"
            variant="warning"
          >
            Funcion Actual
            <b-list-group
              flush
              class="mt-2"
            >
              <b-list-group-item
                class="mt-1"
                variant="light"
              >
                {{ funcionActual.hora_inicio }}
              </b-list-group-item>
              <b-list-group-item
                class="mt-1"
                variant="light"
              >
                {{ funcionActual.contenido.Titulo }}
              </b-list-group-item>
              <b-list-group-item
                class="mt-1"
                variant="light"
              >
                {{ funcionActual.hora_fin }}
              </b-list-group-item>
            </b-list-group>
          </b-list-group-item>
          <b-list-group-item
            class="mt-1"
            variant="secondary"
          >
            Siguiente Funcion: {{ funcionSiguiente.contenido.Titulo }}
          </b-list-group-item>
        </b-list-group>
      </b-card-text>
    </b-card>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "Sala",
  props: {
    sala: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      funcionAnterior: {
        hora_inicio: "Sin Funcion",
        Titulo: "Sin Funcion",
        contenido: { Titulo: "Sin Funcion" }
      },
      funcionActual: {
        hora_inicio: "Sin Funcion",
        hora_fin: "Sin Funcion",
        contenido: { Titulo: "Sin Funcion" }
      },
      funcionSiguiente: {
        hora_fin: "Sin Funcion",
        Titulo: "Sin Funcion",
        contenido: { Titulo: "Sin Funcion" }
      }
    };
  },
  methods: {
    ...mapActions("salasFunciones", [
      "loadFuncionAnterior",
      "loadFuncionActual",
      "loadFuncionSiguiente"
    ]),
    ...mapActions("Catalogo", ["updateSerie", "updatePelicula"])
  },
  computed: {

  },
  async created() {
    let fAnterior = await this.loadFuncionAnterior(this.sala.id);
    let fActual = await this.loadFuncionActual(this.sala.id);
    let fSiguiente = await this.loadFuncionSiguiente(this.sala.id);
    if (fAnterior.funcion) {
      this.funcionAnterior.hora_inicio = fAnterior.funcion.hora_inicio;
      this.funcionAnterior.hora_fin = fAnterior.funcion.hora_fin;
      this.funcionAnterior.contenido = fAnterior.funcion.id_contenido.includes(
        "mov"
      )
        ? await this.updatePelicula(fAnterior.funcion.id_contenido)
        : await this.updateSerie(fAnterior.funcion.id_contenido);
    }
    if (fActual.funcion) {
      this.funcionActual.hora_inicio = fActual.funcion.hora_inicio;
      this.funcionActual.hora_fin = fActual.funcion.hora_fin;
      this.funcionActual.contenido = fActual.funcion.id_contenido.includes(
        "mov"
      )
        ? await this.updatePelicula(fActual.funcion.id_contenido)
        : await this.updateSerie(fActual.funcion.id_contenido);
    }
    if (fSiguiente.funcion) {
      this.funcionSiguiente.hora_inicio = fSiguiente.funcion.hora_inicio;
      this.funcionSiguiente.hora_fin = fSiguiente.funcion.hora_fin;
      this.funcionSiguiente.contenido = fSiguiente.funcion.id_contenido.includes(
        "mov"
      )
        ? await this.updatePelicula(fSiguiente.funcion.id_contenido)
        : await this.updateSerie(fSiguiente.funcion.id_contenido);
    }
  }
};
</script>

