<template>
  <div>
    <b-card
      no-body
      header-tag="header"
      footer-tag="footer"
      :header-bg-variant="estructuraFuncion(funcion.hora_inicio).colorHeader"
      footer-bg-variant="secondary"
    >
      <template v-slot:header>
        <h4>
          <b-badge :variant="estructuraFuncion(funcion.hora_inicio).colorBadge">{{estructuraFuncion(funcion.hora_inicio).orden}} Funcion</b-badge>
        </h4>
      </template>
      <b-list-group>
        <b-list-group-item class="border">{{ funcion.titulo }}</b-list-group-item>
        <b-list-group-item class="border">Sala {{ funcion.numeroSala }}</b-list-group-item>
        <b-list-group-item class="border">{{ funcion.hora_inicio | moment("h:mm:ss a")}}</b-list-group-item>
        <b-list-group-item class="border">{{ funcion.hora_fin | moment("h:mm:ss a")}}</b-list-group-item>
      </b-list-group>
      <template v-slot:footer>
        <b-badge variant="success"><i
            class="fa fa-check"
            aria-hidden="true"
          ></i></b-badge>
      </template>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "Funcion",
  props: {
      funcion: {
          type: Object,
          default: null
      },
  },
  methods: {
    estructuraFuncion(horaInicio) {
      if (horaInicio == "08:00:00")
        return {
          orden: "Primera",
          colorBadge: "primary",
          colorHeader: "info"
        };
      else if (horaInicio == "11:00:00")
        return {
          orden: "Segunda",
          colorBadge: "secondary",
          colorHeader: "success"
        };
      else if (horaInicio == "13:00:00")
        return {
          orden: "Tercera",
          colorBadge: "danger",
          colorHeader: "warning"
        };
      else
        return {
          orden: "Cuarta",
          colorBadge: "light",
          colorHeader: "dark"
        };
    }
  },
};
</script>

<style lang="scss" scoped>
</style>