<template>
  <div class="text-center">
    <h3 class="display-4 my-4">Funciones</h3>
    <b-row>
      <b-col
        cols="3"
        v-for="(funcion, index) in funciones"
        :key="funcion.id"
      >
        <Funcion
          @click="seleccionarFuncion(index)"
          :funcion="funcion"
          :class="[selFuncion === index ? 'shadow-lg' : 'shadow-sm', 'sala', selFuncion === index ? 'border-success' : '', selFuncion === index ? 'bg-light' : '']"
        />
      </b-col>
    </b-row>
  </div>
</template>

<script>
import Funcion from "@/components/SalasYFunciones/Funcion";
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "Funciones",
  components: {
    Funcion
  },
  data() {
    return {
      selFuncion: null
    };
  },
  computed: {
    ...mapGetters("salasFunciones", ["funciones"])
  },
  methods: {
    ...mapMutations("salasFunciones", ["updateFuncion"]),
    seleccionarFuncion(index) {
      this.selFuncion = index;
      this.updateFuncion(this.funciones[index]);
    },
  }
};
</script>

