<template>
  <div class="text-center">
    <h3 class="display-4 my-4">Salas</h3>
    <b-container fluid>
      <b-row no-gutters>
        <b-col
          cols="3"
          v-for="(sala, index) in salas"
          :key="sala.id"
          @click="seleccionarSala(index)"
        >
          <Sala
            :class="[selSala === index ? 'shadow-lg' : 'shadow', 'sala', selSala === index ? 'border-warning' : '', selSala === index ? 'bg-light' : '']"
            :sala="sala"
          />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import Sala from "@/components/SalasYFunciones/Sala.vue";
export default {
  name: "Salas",
  components: {
    Sala
  },
  data() {
    return {
      selSala: null
    };
  },
  computed: {
    ...mapGetters("salasFunciones", ["salas"])
  },
  methods: {
    ...mapMutations("salasFunciones", ["updateSala"]),
    seleccionarSala(index) {
      this.selSala = index;
      this.updateSala(this.salas[index]);
    }
  }
};
</script>

<style lang="scss" scoped>
.sala:hover {
  cursor: pointer;
}
</style>